.body {
    position: relative;
    width: 100%;
    height: 100%;
}

.body > .img {
    width: 100%;
    height: 100%;
}

.trash {
    position: absolute;
    top: 0;
    right: 0;
    width: 1rem;
    height: 1rem;
}

.title {
    position: absolute;
    bottom: 0;
    width: 100%;
}